@charset 'UTF-8';

// Vendors
@import 'vendor/normalize';

@import url('https://fonts.googleapis.com/css?family=Roboto:500&display=swap');
html{
    scroll-behavior: smooth;
} 

body {
  margin:0;
  display:flex;
  justify-content: flex-start;
  flex-direction:column;
  font-size: 13px;
  font-family:'Roboto', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

html, body {
  height: 100%;
} 

a, a:hover, a:visited, a:active {
  color: black;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

.container {
  position: relative;
  &.one .pin {
    background-color: #FFE5E5;
    color: #6A1919;
    &.pinned{
        background-color: #ffcfcf;
    } 
  }
  &.one .triangle {
    background-color: #FFE5E5;
  }

  &.two .pin {
    background-color: #F6E5D4;
    color: #7C4800;
    &.pinned {
      background-color: #f3d6b5;
    }
  }
  &.two .triangle {
    background-color: #F6E5D4;
  }

  &.three .pin {
    background-color: #e3f1ff;
    &.pinned {
      background-color: #C0DEFF;
    }
  }
  &.three .triangle {
    background-color: #e3f1ff;
  }

  &.four .pin {
    background-color: #d5deff;
    &.pinned {
      background-color: #a9bcff;
    }
  }
  &.four .triangle {
    background-color: #d5deff;
  }

  &.five .pin {
    background-color: #d8ffd8;
    &.pinned {
      background-color: #b5ffb5;
    }
  }
  &.five .triangle {
    background-color: #d8ffd8;
  }

  &.six .pin {
    background-color: #dcdcdc;
    &.pinned {
      background-color: #c1c1c1;
    }
  }
  &.six .triangle {
    background-color: #dcdcdc;
  }

  &.seven .pin {
    background-color: #F8E38D;
    &.pinned {
      background-color: #fbda54;
    }
  }
  &.seven .triangle {
    background-color: #F8E38D;
  }
  
  &.eight .pin {
    background-color: #BA94FB;
    &.pinned {
      background-color: #ac7ff9;
    }
  }
  &.eight .triangle {
    background-color: #BA94FB;
  }
    
  &.nine .pin {
    background-color: #d7f9ff;
    &.pinned {
      background-color: #9cf0ff;
    }
  }
  &.nine .triangle {
    background-color: #d7f9ff;
  }

  &.nine {
    margin-bottom: 220px;
  }


}
.items {
    margin-left: 200px;
    margin-left: 158px;
}
.pin{
  height: 20px;
	padding: 4px 10px 0;
	max-width: 650px;
	cursor: pointer;
	white-space: nowrap;
	overflow: hidden !important;
	text-overflow: ellipsis;
}  
.pinned {
  position:sticky;
}
.menu {
  top: 0;
  z-index: 9999;
  left:0;
  width: 80px;
  display:flex; 
  align-items: center;
  justify-content:center;
  position: fixed;
    height: 100vh;
  ul {
    list-style: none;
    margin: 0;
    display: block;
    padding: 0;
      padding: 0;
      li {
          margin: 24px 0;
          position: relative;
          a:after {
            content: '';
            display: block;
            width: 12px;
            height: 12px;
            background-color: black;
            position: absolute;
            border-radius: 50%;
            top: 0;
            left: 72px;
            filter: brightness(90%);
          }
          
      }
  }
}

.menu ul li.one a:after {
  background-color: #FD5557;
}
.menu ul li.two a:after {
  background-color: #AF6E27;
}
.menu ul li.three a:after {
  background-color: #56BED1;
}
.menu ul li.four a:after {
  background-color: #385EF6 ;
}
.menu ul li.five a:after {
  background-color: #007F00 ;
}
.menu ul li.six a:after {
  background-color: #646464 ;
}
.menu ul li.seven a:after {
  background-color: #EFC254;
}
.menu ul li.eight a:after {
  background-color: #845BF6;
}
.menu ul li.nine a:after {
  background-color: #005873;
}
.triangle {
	background: blue;
	clip-path: polygon(100% 0, 0 50%, 100% 100%);
	width: 72px;
	height: 100vh;
	position: fixed;
    left: 127px;
    left: 86px;

    top: 0;
} 

input{
    position: fixed;
	left: 8px;
	top: 8px;
	display: block;
  z-index: 99999;
  width: 100px;
  border: 1px solid black;
  height: 24px;
  padding-left: 8px;
} 

.list {
  display: flex;
	flex-direction: column;
	// justify-content: center;
	// min-height: 100%;
}
.check {
  position: fixed;
  top:70px;
  z-index: 99999;
  input {
    position: fixed;
    top: 34px;
    left: 10px;
    width: 16px;
    height: 60px;
  
  } 
  label {
    display: inline;
	position: fixed;
	left: 30px;
	top: 56px;
  } 
}
.list, .menu {
  height: calc( 100vh - 212px);
}

.displaynone {
    display: none;
}
.policies {
  position: fixed;
	bottom: 0;
  background: white;
  z-index: 9999;
  width: 100%;
}  

.only-selected .list .pin:not(.pinned) {
  display: none !important;
}
